import React, { useEffect, useState } from "react";
import "../App.css";

function AR() {

    const [ date , setDate ] = useState("GETTING DATE");
    const [ showAR , setShowAR ] = useState(false)

    function getDateFromUrl(url) {
        let parsedUrl = new URL(url);
        let params = new URLSearchParams(parsedUrl.search);
        let dateString = params.get('targetTime');
        return dateString ? new Date(dateString) : null;
    }

    function timeUntil(targetDate) {
        let now = new Date();
        let timeDifference = new Date(targetDate) - now; 
        return timeDifference;
    }

    useEffect(
        () => {
            var targetDate = getDateFromUrl(window.location.href)
            setDate( ""+targetDate )
            setTimeout( () => {setShowAR(true)} , timeUntil(targetDate) )
        }, []
    )

    return <div className="App">
        { showAR && <iframe className="Container" id="ArIFrame" title="static_html" src="stage.html"></iframe> }
        { !showAR && <p style={{color:"red",fontSize:"32px"}}>{date}</p> }
    </div>
}

export default AR;