
import { Player } from "textalive-app-api";


const animateWord = function (now, unit) {
  if (unit.contains(now)) {
    //document.querySelector("#text").textContent = unit.text;
    console.log(unit.text)
    _updateDisplayText(unit.text)
  }
};

var player = null;
var _updateDisplayText = () => {}

function initializePlayer(updateDisplayText) {

        _updateDisplayText = updateDisplayText

        player = new Player({
            app: {
            token: "n3DBt0f3zoNmpGVB",
            },
            mediaElement: document.querySelector("#media"),
        });
        
        player.addListener({
            onAppReady,
            onVideoReady,
            onTimerReady,
            onThrottledTimeUpdate,
            onPlay,
            onPause,
            onStop,
        });
}

function play(){
        if (player) {
          if (player.isPlaying) {
            player.requestPause();
          } else {
            player.requestPlay();
          }
        }
        return false;
}

function stop(){
  if (player) {
    console.log("STOPPING")
    player.requestStop();
  }
}

