import React, { useEffect, useState } from "react";
import QRCode from "react-qr-code";
import "../App.css";
import "./Instructions.css";

function calculateTimeout(now) {
    var seconds = 60 - now.getSeconds();
    if( seconds < 30 ){
        seconds += 30
    }
    return seconds*1000;
}

function calculateTargetTime(now, timeout) {
    let targetTime = new Date();
    targetTime.setSeconds(now.getSeconds() + timeout/1000);
    return targetTime;
}

function Instructions(props){

    const local_url = "https://172.20.10.2:3000/arstage"
    const deployed_url = window.location.href
    const isLocalDeployment = false

    var now = new Date();
    var timeout = calculateTimeout(now)

    const [time, setTime] = useState(timeout);
    const [targetTime, setTargetTime] = useState(null);

    useEffect(() => {
        
        setTargetTime(calculateTargetTime(now,timeout))

        var timer = () => {
            if(time > 0){
                setTime((time) => ((time-1000)>0) ? (time-1000) : 0)
                setTimeout(timer,1000);
            }
        }

        props["setStartIn"](timeout + 10500)
        if(time == timeout){
            setTimeout(timer,1000);
        }

    },[])

    return <div className={"InstructionsContainer " + ((time > 0) ? "Show" : "Hide")}>
        <div className="VerticalDividerThird">
        </div>
        <div className="VerticalDividerThird PhoneBG">
            <div className="QRCodeContainer">
                { targetTime!=null && <QRCode value={(isLocalDeployment?local_url:deployed_url).concat("?targetTime=").concat(targetTime.toISOString())} fgColor="#2069c9"/> }
            </div>        
            <div className="Marker" />
        </div>
        <div className="VerticalDividerThird">
            <div className="Scan Float"/>
            <div className="View Float"/>
        </div>
        <div className="CountDown">
            {/* {("0" + Math.floor((time / 60000) % 60)).slice(-2)}
            :{("0" + Math.floor((time / 1000) % 60)).slice(-2)}
            .{("0" + ((time / 10) % 100)).slice(-2)} */}

            Starting in {("0" + Math.floor((time / 1000) % 60)).slice(-2)}...
        </div>
    </div>
}

export default Instructions;