import React from "react";
import "../App.css"
import StylisedButton from "../components/StylisedButton";

function Home(props){

    const navToStage = () => {
        props.navigate("Stage")
    }
    const navToDemo = () => {
        props.navigate("Demo")
    }

    return (
        <div className="Container">
            <div className="Note1" />
            <div className="Note2" />
            <div className="TopBanner">MikuProCon24</div>
            <div className="TitleText" />
            <div className="MakuhariSilhouette" />
            <div className="HomeButtonContainer">
                <StylisedButton text="Begin!" style="PrimaryColor" onClick={navToStage}/>
                <StylisedButton text="Watch Demo!" style="SecondaryColor" onClick={navToDemo}/>
            </div>
        </div>
    )
}

export default Home